<h2 id="select-下拉选择" tabindex="-1"><a class="header-anchor" href="#select-下拉选择" aria-hidden="true">#</a> Select 下拉选择</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="单选" tabindex="-1"><a class="header-anchor" href="#单选" aria-hidden="true">#</a> 单选</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-select
      v-model="selectedValue1"
      @change="selectedChangeHandler1"
      placeholder="请选择搜索引擎"
      size="small"
      key="s2"
    >
      <wd-option label="百度" value="baidu"></wd-option>
      <wd-option label="谷歌" value="google"></wd-option>
      <wd-option label="必应" value="bing"></wd-option>
    </wd-select>
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">单选下拉框</div>
    <div class="component-desc">
      如果没有设置multiple或者multiple=false，则为单选下拉框
    </div>
  </div>
</div>
