<h3 id="属性" tabindex="-1"><a class="header-anchor" href="#属性" aria-hidden="true">#</a> 属性</h3>
<table>
<thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">clearable</td>
<td style="text-align:center">是否可清空</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">disabled</td>
<td style="text-align:center">是否可用</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">true</td>
</tr>
<tr>
<td style="text-align:center">filterable</td>
<td style="text-align:center">是否可搜索</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">multiple</td>
<td style="text-align:center">是否为多选</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">multiple-limit</td>
<td style="text-align:center">多选项个数显示</td>
<td style="text-align:center">number</td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:center">size</td>
<td style="text-align:center">组件大小</td>
<td style="text-align:center">string</td>
<td style="text-align:center">small / large / default</td>
<td style="text-align:center">default</td>
</tr>
</tbody>
</table>
<h3 id="事件" tabindex="-1"><a class="header-anchor" href="#事件" aria-hidden="true">#</a> 事件</h3>
<table>
<thead>
<tr>
<th style="text-align:center">事件名称</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">change</td>
<td style="text-align:center">值变化时触发</td>
<td style="text-align:center">Function(selectedValue)</td>
</tr>
<tr>
<td style="text-align:center">remote-tag</td>
<td style="text-align:center">多选时删除已选项时触发</td>
<td style="text-align:center">() =&gt; void</td>
</tr>
<tr>
<td style="text-align:center">clear</td>
<td style="text-align:center">清空选择的值时触发</td>
<td style="text-align:center">() =&gt; void</td>
</tr>
<tr>
<td style="text-align:center">visible-change</td>
<td style="text-align:center">下拉选项显示与隐藏</td>
<td style="text-align:center">Function(visibleValue)</td>
</tr>
</tbody>
</table>
